}

.joongna-chat {
  color: #3498db;
  font-weight: 600;
  padding: 0.2rem 0.5rem;
  background: rgba(52, 152, 219, 0.1);
  border-radius: 12px;
  font-size: 0.85rem;
}

.joongna-parcel {
  color: #9b59b6;
  font-weight: 600;
  padding: 0.2rem 0.5rem;
  background: rgba(155, 89, 182, 0.1);
  border-radius: 12px;
  font-size: 0.85rem;
}

.source-badge.번개장터 {
  background: #ff1744;
}

.cafe-name {
  color: #7f8c8d;
  font-size: 0.9rem;
