  flex: 1;
  background-color: #ffffff;
  border-right: 2px solid #e1e8ed;
  min-width: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.sub-category-panel {
  flex: 1;
  background-color: #f8f9fa;
  border-right: 2px solid #e1e8ed;
  min-width: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.third-level-panel {
  flex: 1;
  background-color: #f0f8f0;
  min-width: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.panel-header {
  padding: 0.75rem 1rem;
  background-color: #f1f3f4;
  border-bottom: 1px solid #e1e8ed;
  font-weight: 600;
  font-size: 0.85rem;
  color: #495057;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
}

.loading-indicator {
  font-size: 0.8rem;
  margin-left: 0.5rem;
}

.selected-main {
  font-size: 0.75rem;
  color: #1976d2;
  margin-left: 0.5rem;
  font-weight: normal;
}

.selected-sub {
  font-size: 0.75rem;
  color: #2e7d32;
  margin-left: 0.5rem;
  font-weight: normal;
}

.main-category-panel .category-list,
.sub-category-panel .category-list,
.third-level-panel .category-list {
  height: 100%;
  overflow-y: auto;
  padding: 0;
}

/* 중복 제거됨 - 위에서 이미 정의됨 */

.has-children {
  font-size: 0.7rem;
  color: #6c757d;
  margin-left: auto;
}

.no-subcategories {
  padding: 2rem 1rem;
  text-align: center;
  color: #6c757d;
  font-style: italic;
}

.no-subcategories p {
  margin: 0;
  font-size: 0.9rem;
}

/* 스크롤바 스타일링 */
.main-category-panel .category-list::-webkit-scrollbar,
.sub-category-panel .category-list::-webkit-scrollbar,
.third-level-panel .category-list::-webkit-scrollbar {
  width: 6px;
}

.main-category-panel .category-list::-webkit-scrollbar-track,
.sub-category-panel .category-list::-webkit-scrollbar-track,
.third-level-panel .category-list::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.main-category-panel .category-list::-webkit-scrollbar-thumb,
.sub-category-panel .category-list::-webkit-scrollbar-thumb,
.third-level-panel .category-list::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.main-category-panel .category-list::-webkit-scrollbar-thumb:hover,
.sub-category-panel .category-list::-webkit-scrollbar-thumb:hover,
.third-level-panel .category-list::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

